import React from 'react';
import { connect } from 'dva';
import { Layout, Row, Col, Button } from 'antd';
import BaseComponent from 'components/BaseComponent';
import Panel from 'components/Panel';
import Print from 'components/Print';
import EC from 'components/Charts/ECharts/EC';
import 'echarts/lib/chart/pie';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import Report from './Report';
const { Content } = Layout;

@connect()
export default class Blank extends BaseComponent {
  state = {
    element1: null,
    element2: null
  };

  saveElement1 = node => {
    this.setState({
      element1: node
    });
  };

  saveElement2 = node => {
    this.setState({
      element2: node
    });
  };

  getOption = () => ({
    title: {
      text: '',
      subtext: '',
      x: 'center'
    },
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b} : {c} ({d}%)'
    },
    legend: {
      orient: 'vertical',
      left: 'left',
      data: ['', '', '', '', '']
    },
    series: [
      {
        name: '',
        type: 'pie',
        radius: '55%',
        center: ['50%', '60%'],
        data: [
          { value: 335, name: '' },
          { value: 310, name: '' },
          { value: 234, name: '' },
          { value: 135, name: '' },
          { value: 1548, name: '' }
        ],
        itemStyle: {
          emphasis: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      }
    ]
  });

  render() {
    const { element1, element2 } = this.state;
    const comps = (
      <div>
        <table border="1">
          <thead>
            <tr>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td></td>
              <td></td>
            </tr>
          </tbody>
        </table>
      </div>
    );
    return (
      <Layout className="full-layout page print-page">
        <Content>
          <Panel title="" ref={this.saveElement1}>
            <h3>Print </h3>
            <p>HTMLdomReact</p>
          </Panel>
          <Row gutter={20}>
            <Col span={12}>
              <Panel title=" & HTML">
                <div>{`<span style="color: red"></span>`}</div>
                <br />
                <Print content={`<span style="color: red"></span>`} />
              </Panel>
              <Panel title="React">
                <div>{comps}</div>
                <br />
                <Print
                  trigger={<Button icon="printer"></Button>}
                  content={comps}
                />
              </Panel>
              <Panel title="">
                <div></div>
                <br />
                <Print
                  trigger={<Button icon="printer"></Button>}
                  content={<Report />}
                />
              </Panel>
            </Col>
            <Col span={12}>
              <Panel title="ref  DOM  | React ">
                <div></div>
                <br />
                {element1 ? (
                  <Print
                    trigger={<Button icon="printer"></Button>}
                    content={element1}
                  />
                ) : null}
              </Panel>
              <Panel title="Canvas">
                <div style={{ height: 300 }}>
                  <EC option={this.getOption()} ref={this.saveElement2} />
                </div>
                <br />
                {element2 ? (
                  <Print
                    canvas
                    trigger={<Button icon="printer"></Button>}
                    content={element2}
                  />
                ) : null}
              </Panel>
            </Col>
          </Row>
        </Content>
      </Layout>
    );
  }
}



